@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.navbar__main {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.navbar__main-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.navbar__main-links_logo {
    margin-right: 2rem;

}

.navbar_main-links_logo img {
    width: 70px;
    height: 20px;
}

.navbar__main-links_container {
    display: flex;
    flex-direction: row;
}

.navbar__main-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar__main-links_container a{
    /* font-family: var(--font-family); */
    /* font-weight: 500; */
    /* font-size: 18px; */
    /* line-height: 25px; */
    
    text-decoration: none;

    margin: 0 1rem;
    cursor: pointer;
}


.navbar__main-sign button {
    padding: 0.5rem .75rem;
    font-family: var(--font-family);
    font-weight: 500;
    
}

.navbar__main-menu {
    margin-left: 1rem;

    display: none;
    position: relative;

}
.resume_button:hover {
    color: rgba(255,105,180,0.8);
}
.header__main {
  display: flex;
  align-items: center;
  height: 800px;
  -webkit-transform: translateX(-14%);
          transform: translateX(-14%);
}

.header__main-name {
  align-items: left;
  -webkit-transform: translate(100%, -50%);
          transform: translate(100%, -50%);
}

.header__main-name_sub {
  color: rgba(255, 105, 180, 0.8);
}

.header__main-name h1 {
  color: #25449f;
}

.header__main-links {
  flex: 1 1;
  display: flex;
  justify-content: left;
  text-decoration: none;
  box-sizing: content-box;
}

.header__main-links_linkedin {
  padding-right: 1rem;
}

.header__main-links_instagram {
  padding-right: 1rem;
}

.header__main-links_github {
  padding-right: 1rem;
}
.header__main-links_github:hover,
.header__main-links_instagram:hover,
.header__main-links_linkedin:hover {
  color: rgba(255, 105, 180, 0.8);
}

.header__main-links Link {
  cursor: pointer;
}

.about__main {

    /* align-items: center; */
    -webkit-transform: translatex(20%);
            transform: translatex(20%);
    max-width: 700px;
    padding: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.about__main-text {
    display: flex;
    flex-direction: column;
    padding-right: 10rem;
    min-width: -webkit-fill-available;
}

.about__main-body {
    align-items: left;
}

.about__main-tech_list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about__main-image {
    width: 300px;
    height: 300px;
    padding-left: 5rem;
}

.ItemCard__main-header-link:hover {
	color: rgba(255, 105, 180, 0.8);
}
.projects__main {
	width: 60%;

	margin-top: 4rem;
	-webkit-transform: translateX(30%);
	        transform: translateX(30%);
}
.ItemCard__main:hover {
	-webkit-transform: translateY(-2%);
	        transform: translateY(-2%);
	box-shadow: 0px 0px 10px rgba(255, 105, 180, 0.8);
}

.projects__main-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-row-gap: 1rem;
	row-gap: 1rem;
	margin-top: 1rem;
	justify-items: center;
	position: relative;
}

.ItemCard__main {
	width: 260px;
	height: 260px;
	border-radius: 10px;
	margin-right: 0rem;
	text-align: center;
}
.ItemCard__main-content {
	text-align: center;
}
footer {
	display: block;
}
.wrapper {
	text-align: center;
	display: inline-block;
}
.ItemCard__main-content-list {
	padding: 0px;
	margin-top: 30px;
	display: flex;
	flex-direction: row;
}
.ItemCard__main-content-text {
	height: 100px;
}
.MuiPaper-elevation {
	background-color: rgba(230, 230, 255, 0.5);
	max-width: -webkit-max-content;
	max-width: max-content;
	margin: 2px;
	padding-left: 2px;
	padding-right: 2px;
	text-shadow: 10px;
}
.MuiPaper-elevation:hover {
	-webkit-transform: translateY(-1%);
	        transform: translateY(-1%);
	box-shadow: 0px 0px 2px rgba(255, 105, 180, 0.8);
}


.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


.about {
    -webkit-animation: fadeIn 2s;
            animation: fadeIn 2s;
}
.projects {
    -webkit-animation: fadeIn 2s;
            animation: fadeIn 2s;
}
.tempAbout {
    height: 500px;
}
.tempProjects {
    height: 570px;
}
@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    0% {
        -webkit-transform: translateY(150px);
                transform: translateY(150px);
    }

    100% {
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    0% {
        -webkit-transform: translateY(150px);
                transform: translateY(150px);
    }

    100% {
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

html {
    overflow: hidden;
    margin: 0px;
    padding: 0px;
    height: 100%;
}

body {
    overflow-x: hidden;
    margin: 0px;
    padding: 0px;
    height: 100%;
    background: rgb(201, 223, 244);
    background: radial-gradient(circle, rgba(201, 223, 244, 1) 0%, rgba(188, 227, 242, 1) 21%, rgba(194, 203, 231, 1) 73%, rgba(198, 183, 223, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#c9dff4", endColorstr="#c6b7df", GradientType=1);
}



:root {
    --font-family: 'Manrope', sans-serif;

    --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);

    --color-bg: #1f1f38;
    --color-footer: #031B34;
    --color-blog: #042c54;
    --color-text: #81AFDD;
    --color-subtext: #FF8A71;
}

